import React from 'react'
import UserDashboard from '../pages/UserDashboard'
import MaidDashboard from '../pages/MaidDashboard'

const Dashboard = ({ user, serviceUser, provider, setProvider, setServiceUser }) => {
  if (user.type === 'user')
    return <UserDashboard user={serviceUser} setServiceUser={setServiceUser} />
  else
    return <MaidDashboard user={user} provider={provider} setProvider={setProvider} />
}

export default Dashboard
